<template>
  <view>
    <view class="editor_toolbox">
      <i class="iconfont icon-undo" data-method="undo" @tap="edit" />
      <i class="iconfont icon-redo" data-method="redo" @tap="edit" />
      <i class="iconfont icon-img" data-method="insertImg" @tap="edit" />
      <i class="iconfont icon-video" data-method="insertVideo" @tap="edit" />
      <i class="iconfont icon-link" data-method="insertLink" @tap="edit" />
      <i class="iconfont icon-text" data-method="insertText" @tap="edit" />
      <i class="iconfont icon-clear" @tap="clear" />
      <i class="iconfont icon-save" @tap="save" />
    </view>
	
    <view style="padding-top: 50px;">
      <mp-html
          ref="article"
          :content="content"
          :editable="editable"
          container-style="padding:20px"
          domain="https://mp-html.oss-cn-hangzhou.aliyuncs.com"
          @remove="remove" />
    </view>

    <block v-if="modal">
      <view class="mask" />
      <view class="modal">
        <view class="modal_title">{{modal.title}}</view>
        <input :value="modal.value" auto-focus class="modal_input" maxlength="-1" @input="modalInput" />
        <view class="modal_foot">
          <view class="modal_button" @tap="modalCancel">取消</view>
          <view class="modal_button" style="color:#576b95;border-left:1px solid rgba(0,0,0,.1)"
                @tap="modalConfirm">确定</view>
        </view>
      </view>
    </block>

  </view>
</template>

<script>
// todo 表单输入并验证，填写文章基本信息
// todo 完成之后，跳转文章正文写作页面【要支持富文本编辑】
// todo 写作页面，需要登录才能使用
import mpHtml from '@/components/mp-html/mp-html'
import content from './content'
// 上传图片方法
function upload(src, type) {
  return new Promise((resolve, reject) => {
    console.log('上传', type === 'img' ? '图片' : '视频', '：', src)
    resolve(src)
    /*
    // 实际使用时，上传到服务器
    wx.uploadFile({
      url: 'xxx', // 接口地址
      filePath: src,
      name: 'xxx',
      success(res) {
        resolve(res.data.path) // 返回线上地址
      },
      fail: reject
    })
    */
  })
}
// 删除图片方法
function remove(src) {
  console.log('删除图片：', src)
  // 实际使用时，删除线上资源
}
export default {
  data() {
    return {
      content,
      modal: null,
      editable: true
    }
  },
  components: {
    mpHtml
  },
  onReady() {
    /**
     * @description 设置获取链接的方法
     * @param {String} type 链接的类型（img/video/audio/link）
     * @param {String} value 修改链接时，这里会传入旧值
     * @returns {Promise} 返回线上地址
     *   type 为音视频时可以返回一个数组作为源地址
     *   type 为 audio 时，可以返回一个 object，包含 src、name、author、poster 等字段
     */
    this.$refs.article.getSrc = (type, value) => {
      return new Promise((resolve, reject) => {
        if (type === 'img' || type === 'video') {
          uni.showActionSheet({
            itemList: ['本地选取', '远程链接'],
            success: res => {
              if (res.tapIndex === 0) {
                // 本地选取
                if (type === 'img') {
                  uni.chooseImage({
                    count: value === undefined ? 9 : 1, // 2.2.0 版本起插入图片时支持多张（修改图片链接时仅限一张）
                    success: res => {
                      // #ifdef MP-WEIXIN
                      if (res.tempFilePaths.length == 1 && wx.editImage) {
                        // 单张图片时进行编辑
                        wx.editImage({
                          src: res.tempFilePaths[0],
                          complete: res2 => {
                            uni.showLoading({
                              title: '上传中'
                            })
                            upload(res2.tempFilePath || res.tempFilePaths[0], type).then(res => {
                              uni.hideLoading()
                              resolve(res)
                            })
                          }
                        })
                      } else {
                        // #endif
                        uni.showLoading({
                          title: '上传中'
                        });
                        (async () => {
                          const arr = []
                          for (let item of res.tempFilePaths) {
                            // 依次上传
                            const src = await upload(item, type)
                            arr.push(src)
                          }
                          return arr
                        })().then(res => {
                          uni.hideLoading()
                          resolve(res)
                        })
                        // #ifdef MP-WEIXIN
                      }
                      // #endif
                    },
                    fail: reject
                  })
                } else {
                  uni.chooseVideo({
                    success: res => {
                      uni.showLoading({
                        title: '上传中'
                      })
                      upload(res.tempFilePath, type).then(res => {
                        uni.hideLoading()
                        resolve(res)
                      })
                    },
                    fail: reject
                  })
                }
              } else {
                // 远程链接
                this.callback = {
                  resolve,
                  reject
                }
                this.$set(this, 'modal', {
                  title: (type === 'img' ? '图片' : '视频') + '链接',
                  value
                })
              }
            }
          })
        } else {
          this.callback = {
            resolve,
            reject
          }
          let title
          if (type === 'audio') {
            title = '音频链接'
          } else if (type === 'link') {
            title = '链接地址'
          }
          this.$set(this, 'modal', {
            title,
            value
          })
        }
      })
    }
  },
  methods: {
    // 删除图片/视频/音频标签事件
    remove(e) {
      // 删除线上资源
      remove(e.src)
    },
    // 处理模态框
    modalInput(e) {
      this.value = e.detail.value
    },
    modalConfirm() {
      this.callback.resolve(this.value || this.modal.value || '')
      this.$set(this, 'modal', null)
    },
    modalCancel() {
      this.callback.reject()
      this.$set(this, 'modal', null)
    },
    // 调用编辑器接口
    edit(e) {
      this.$refs.article[e.currentTarget.dataset.method]()
    },
    // 清空编辑器内容
    clear() {
      uni.showModal({
        title: '确认',
        content: '确定清空内容吗？',
        success: res => {
          if (res.confirm)
            this.$refs.article.clear()
        }
      })
    },
    // 保存编辑器内容
    save() {
      setTimeout(() => {
        var content = this.$refs.article.getContent()
        uni.showModal({
          title: '保存',
          content,
          confirmText: '完成',
          success: res => {
            if (res.confirm) {
              // 复制到剪贴板
              uni.setClipboardData({
                data: content,
              })
              // 结束编辑
              this.editable = false
            }
          }
        })
      }, 50)
    }
  }
}
</script>

